import { load_products } from "../actions/products";

const getConfi = (
    choosedSubCategories,
    choosedBrand,
    searchedCategory,
    choosedOrder,
    loadMore,
    searchValue
) => {
    let choosedSubCategoriesApi = choosedSubCategories.map((subcategory) => {
        return `subcategory_ids=${subcategory}`;
    });
    choosedSubCategoriesApi = choosedSubCategoriesApi.join("&");

    let choosedBrandsApi = choosedBrand.map((choosedBrand) => {
        return `brand_ids=${choosedBrand}`;
    });
    choosedBrandsApi = choosedBrandsApi.join("&");

    const confi = `category_id=${searchedCategory ? searchedCategory?.id : ""}${
        choosedOrder.value && `&ordering=${choosedOrder?.value}`
    }${choosedSubCategories.length >= 1 ? `&${choosedSubCategoriesApi}` : ""}${
        choosedBrand.length >= 1 ? `&${choosedBrandsApi}` : ""
    }${searchValue ? `&search=${searchValue.trim()}` : ""}${
        loadMore ? `&limit=20&offset=${loadMore}` : "&limit=10&available=true"
    }`;
    // console.log("Confi ", confi);
    return confi;
};

export const getData = (
    dispatch,
    setIsLoading,
    setLoadMore,
    loadMore,
    products,
    choosedSubCategories,
    choosedBrand,
    searchedCategory,
    choosedOrder,
    lang
) => {
    setIsLoading(true);
    const confi = getConfi(
        choosedSubCategories,
        choosedBrand,
        searchedCategory,
        choosedOrder,
        loadMore
    );
    if (loadMore) {
        dispatch(load_products(confi, products, lang)).then((res) => {
            setIsLoading(false);
            setLoadMore(false);
        });
    } else {
        dispatch(load_products(confi, null, lang)).then((res) => {
            setIsLoading(false);
        });
    }
};

export const getSearchData = (
    dispatch,
    setIsLoading,
    setLoadMore,
    loadMore,
    products,
    choosedSubCategories,
    choosedBrand,
    searchedCategory,
    choosedOrder,
    lang,
    searchValue
) => {
    setIsLoading(true);
    const confi = getConfi(
        choosedSubCategories,
        choosedBrand,
        searchedCategory,
        choosedOrder,
        loadMore,
        searchValue
    );

    if (loadMore) {
        dispatch(load_products(confi, products, lang)).then((res) => {
            setIsLoading(false);
            setLoadMore(null);
        });
    } else {
        dispatch(load_products(confi, null, lang)).then((res) => {
            setIsLoading(false);
        });
    }
};
