import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import BigHeader from "../components/Products/BigHeader";
import Breadcumb from "../components/Products/Breadcumb";
import FiltersRow from "../components/Products/FiltersRow";
import MiddleFitlers from "../components/Products/MiddleFitlers";
import ProductsMap from "../components/Products/ProductsMap";
import SubcategoriesRow from "../components/Products/SubcategoriesRow";
import { get_brands, get_subcategories } from "../redux/actions/products";
import { selectCategories, selectProducts } from "../redux/selectors/products";
import { getSearchData } from "../redux/utils/products";

const mapState = (state) => ({
	categories: selectCategories(state),
	products: selectProducts(state),
	prev: state.products.prev,
	next: state.products.next,
	count: state.products.count,
});

function Search() {
	const { t, i18n } = useTranslation();
	// Redux
	const { categories, products, count } = useSelector(mapState);
	// Search
	const search = useLocation().search;
	const searchCategory = new URLSearchParams(search).get("category");
	const searchQuery = new URLSearchParams(search).get("query");
	// States
	// For searching category
	const [choosedCategory, setChoosedCategory] = useState(null);

	// All subcategories for searching category
	const [subCategories, setSubCategories] = useState([]);
	// All brands
	const [brands, setBrands] = useState([]);

	// For subcategories that user choosed to search in
	const [choosedSubCategories, setChoosedSubCategories] = useState([]);
	// For Brands that user choosed to search in
	const [openFilters, setOpenFilters] = useState(null);
	const [choosedBrands, setChoosedBrands] = useState([]);

	// For ordering products
	const [choosedOrder, setChoosedOrder] = useState({
		name: "default",
		value: "",
	});
	// If user wants more
	const [loadMore, setLoadMore] = useState(null);
	const [clickedLoadMore, setClickedLoadMore] = useState(false);
	// If it request sent or not
	const [isLoading, setIsLoading] = useState(false);
	// Input value
	const [searchValue, setSearchValue] = useState(
		searchQuery ? searchQuery : ""
	);
	// End states
	const dispatch = useDispatch();
	const handleCategoryChange = (categoryValue, categoryName) => {
		setChoosedCategory({
			id: categoryValue,
			name: categoryName,
		});
	};
	const getdata = () => {
		getSearchData(
			dispatch,
			setIsLoading,
			setLoadMore,
			loadMore,
			products,
			choosedSubCategories,
			choosedBrands,
			choosedCategory,
			choosedOrder,
			i18n.language,
			searchValue
		);
	};

	const handleSubmit = (e) => {
		e.preventDefault();
		if (searchValue.length >= 3) {
			getdata();
		}
	};

	useEffect(() => {
		window.scrollTo({
			top: 0,
			behavior: "smooth",
		});
		if (searchQuery) {
			setSearchValue(searchQuery);
		}
		if (searchCategory) {
			const found = categories.find(
				(category) => category.name === searchCategory
			);
			if (found) {
				handleCategoryChange(found.id, found.name);
			} else {
				handleCategoryChange("", "All");
			}
		} else {
			handleCategoryChange("", "All");
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [searchQuery, searchCategory]);

	useEffect(() => {
		if (choosedCategory?.id >= 1) {
			dispatch(get_subcategories(choosedCategory?.id, i18n.language)).then(
				(result) => {
					setSubCategories(result);
				}
			);
			dispatch(get_brands(choosedCategory?.id, i18n.language)).then(
				(result) => {
					setBrands(result);
				}
			);
		} else {
			setSubCategories([]);
			setBrands([]);
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [choosedCategory, i18n.language]);

	useEffect(() => {
		if (clickedLoadMore) {
			setClickedLoadMore(false);
		} else {
			getdata();
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [
		choosedCategory,
		categories,
		choosedOrder,
		choosedSubCategories,
		choosedBrands,
		clickedLoadMore,
		i18n.language,
	]);
	return (
		<div>
			<BigHeader
				searchedCategoryName={searchValue.length >= 1 ? searchValue : "Search"}
			/>
			<div className="container">
				<Breadcumb
					topmar={true}
					searchedCategoryName={
						searchValue.length >= 1 ? searchValue : "Search"
					}
				/>
				<div className="row mb-0">
					<div className="py-5  mx-auto col-md-6">
						<div
							class="input-group  "
							style={{
								boxShadow: "0 10px 30px 0 rgba(68, 55, 77, 0.3)",
								borderRadius: "0.85rem",
							}}
						>
							<button
								style={{
									borderBottomLeftRadius: "0.85rem",
									borderTopLeftRadius: "0.85rem",
									borderTopRightRadius: "0",
									borderBottomRightRadius: "0",
								}}
								class="btn btn-light border-0 dropdown-toggle py-2 px-3"
								type="button"
								data-bs-toggle="dropdown"
								aria-expanded="false"
							>
								{choosedCategory?.name && choosedCategory?.name === "All"
									? t("common:All")
									: choosedCategory?.name}
							</button>
							<ul
								className="dropdown-menu scroll-y mt-2"
								style={{
									maxHeight: "300px",
									overflowY: "scroll",
									maxWidth: "100px !important",
									borderRadius: "15px",
								}}
							>
								<li
									className="dropdown-item"
									role="button"
									onClick={() => handleCategoryChange("", "All")}
								>
									{t("common:All")}
								</li>
								{categories.length >= 1 &&
									categories.map((category) => {
										return (
											<li
												key={category?.id}
												className="dropdown-item truncate-overflow-1 "
												role="button"
												onClick={() =>
													handleCategoryChange(category?.id, category?.name)
												}
											>
												{category?.name}
											</li>
										);
									})}
							</ul>
							<div className="form-floating col">
								<input
									type="text"
									class="form-control "
									id="searchInputValue"
									placeholder="barista"
									value={searchValue}
									onChange={(e) => setSearchValue(e.target.value)}
									style={{
										borderBottomRightRadius: "0.85rem",
										borderTopRightRadius: "0.85rem",
										borderTopLeftRadius: "0",
										borderBottomLeftRadius: "0",
										border: "none",
									}}
									onKeyDown={(e) => {
										if (e.key === "Enter") {
											handleSubmit(e);
										}
									}}
									aria-label="Amount (to the nearest dollar)"
								/>
								<label for="searchInputValue">Search</label>
							</div>
							<button
								onClick={(e) => handleSubmit(e)}
								style={{ right: "10px", borderRadius: "30px" }}
								className="btn border-0 btn-outline-dark-blue  mt-2 position-absolute"
							>
								{isLoading ? (
									<div class="spinner-border text-primary" role="status">
										<span class="visually-hidden">Loading...</span>
									</div>
								) : (
									<i class="bi bi-search"></i>
								)}
							</button>
						</div>
					</div>
				</div>
				{subCategories && subCategories.length >= 1 && (
					<SubcategoriesRow
						subCategories={subCategories}
						choosedSubCategories={choosedSubCategories}
						setChoosedSubCategories={setChoosedSubCategories}
						page="search"
					/>
				)}
				<FiltersRow
					count={count}
					middleFilters={openFilters}
					setMiddleFilters={setOpenFilters}
					choosedOrder={choosedOrder}
					setChoosedOrder={setChoosedOrder}
				/>
				{openFilters && (
					<MiddleFitlers
						middleFilters={openFilters}
						brands={brands}
						setChoosedBrand={setChoosedBrands}
						choosedBrand={choosedBrands}
					/>
				)}
				<ProductsMap
					setClickedLoadMore={setClickedLoadMore}
					setLoadMore={setLoadMore}
					isLoading={isLoading}
				/>
			</div>
		</div>
	);
}

export default Search;
