import React from "react";
import { useTranslation } from "react-i18next";

function FiltersRow({
	count,
	middleFilters,
	setMiddleFilters,
	choosedOrder,
	setChoosedOrder,
}) {
	const { t } = useTranslation(["common"]);
	const handleOrderChange = (name, value) => {
		setChoosedOrder({
			name: name,
			value: value,
		});
	};
	return (
		<div class="row">
			<div className="row align-items-center justify-content-between mb-1">
				<div className="col-12 col-md-auto  py-2">
					<h5>
						{count} {t("common:items_found")}
					</h5>
				</div>
				<div className="col-auto  d-flex ">
					<button
						type="button"
						className={`badge   ${
							middleFilters ? "subcat-badge-active" : "subcat-badge"
						} btn-light  border me-2 my-1  border`}
						style={{
							outline: "none",
							borderRadius: "10px",
							padding: "0.60rem 0.90rem",
							fontSize: "18px",
							fontWeight: "500",
						}}
						onClick={() =>
							middleFilters
								? setMiddleFilters(null)
								: setMiddleFilters("Brands")
						}
						aria-expanded="false"
					>
						{t("common:brands")} <i class="bi bi-caret-down-fill"></i>
					</button>

					<button
						type="button"
						className="dropdown-toggle truncate-overflow-1 badge  bg-dark text-white border me-2 my-1 subcat-badge border"
						data-bs-toggle="dropdown"
						style={{
							outline: "none",
							borderRadius: "10px",
							padding: "0.60rem 0.90rem",
							fontSize: "18px",
							fontWeight: "500",
						}}
						aria-expanded="false"
					>
						{t(`common:${choosedOrder?.name}`)}
					</button>
					<ul className="dropdown-menu" style={{ borderRadius: "20px" }}>
						<li
							className="dropdown-item"
							role="button"
							onClick={() => handleOrderChange("default", "")}
						>
							{t("common:default")}
						</li>
						<li
							className="dropdown-item"
							role="button"
							onClick={() => handleOrderChange("cheap_to_exp", "new_price")}
						>
							{t("common:cheap_to_exp")}
						</li>
						<li
							className="dropdown-item"
							role="button"
							onClick={() => handleOrderChange("exp_to_cheap", "-new_price")}
						>
							{t("common:exp_to_cheap")}
						</li>
						<li
							className="dropdown-item"
							role="button"
							onClick={() => handleOrderChange("new", "-created")}
						>
							{t("common:new")}
						</li>
						<li
							className="dropdown-item"
							role="button"
							onClick={() => handleOrderChange("top_looked", "created")}
						>
							{t("common:top_looked")}
						</li>
					</ul>
				</div>
			</div>
			<hr style={{ color: "#6270FB" }} />
		</div>
	);
}

export default FiltersRow;
