import React from "react";

function SubcategoriesRow({
	subCategories,
	choosedSubCategories,
	setChoosedSubCategories,
	page,
}) {
	const handleSubCatAdd = (id) => {
		setChoosedSubCategories([...choosedSubCategories, id]);
	};
	const handleSubCatRemove = (id) => {
		const reduced = choosedSubCategories.filter(
			(choosedsub) => choosedsub !== id
		);
		setChoosedSubCategories([...reduced]);
	};
	return (
		<div
			className={`row row-cols-auto align-items-center  px-3 px-md-0 scroll-y ${
				page === "search" ? "py-3" : "pt-5 pb-4"
			}`}
			style={{ overflowY: "scroll", maxHeight: "400px" }}
		>
			{Array.isArray(subCategories) &&
				subCategories.length >= 1 &&
				subCategories.map((subcategory) => {
					return (
						<span
							key={subcategory?.id}
							className={
								choosedSubCategories.includes(subcategory?.id)
									? "badge rounded-pill border me-2 my-1 subcat-badge-active "
									: "badge rounded-pill border me-2 my-1 subcat-badge"
							}
							onClick={
								choosedSubCategories.includes(subcategory?.id)
									? () => handleSubCatRemove(subcategory?.id)
									: () => handleSubCatAdd(subcategory?.id)
							}
							role="button"
							style={{
								padding: "0.50rem 0.80rem",
								fontSize: "16px",
								fontWeight: "500",
							}}
						>
							{subcategory?.name}
						</span>
					);
				})}
		</div>
	);
}

export default SubcategoriesRow;
