import React from "react";

function MiddleFitlers({
	brands,
	middleFilters,
	setChoosedBrand,
	choosedBrand,
}) {
	const handleChoosedBrandAdd = (id) => {
		setChoosedBrand([...choosedBrand, id]);
	};
	const handleChoosedBrandRemove = (id) => {
		const reduced = choosedBrand.filter((choosedBrand) => choosedBrand !== id);
		setChoosedBrand([...reduced]);
	};
	return (
		<div
			className="row pb-3 px-3 px-md-0 scroll-y"
			style={{ maxHeight: "250px", overflowY: "scroll" }}
		>
			{middleFilters === "Brands" &&
				brands.length >= 1 &&
				brands.map((brand) => {
					return (
						<div
							key={brand.id}
							class="col-auto form-check form-switch me-1 my-1"
						>
							<input
								class="form-check-input"
								type="checkbox"
								id={brand.id}
								onChange={
									choosedBrand.includes(brand.id)
										? () => handleChoosedBrandRemove(brand.id)
										: () => handleChoosedBrandAdd(brand.id)
								}
								checked={choosedBrand.includes(brand.id)}
							/>
							<label class="form-check-label" htmlFor={brand.id}>
								{brand.name}
							</label>
						</div>
					);
				})}
		</div>
	);
}

export default MiddleFitlers;
